import React, { Component } from 'react'
import styles from './ContactForm.module.css';

class ContactForm extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             fullname: '',
             email: '',
             comments: ''
        }
    }

    handleUserNameChange = (event) => {
        this.setState({
            fullname: event.target.value
        })
    }
    handleCommentsChange = (event) => {
        this.setState({
            comments: event.target.value
        })
    }
    handleEmailChange = event => {
        this.setState({
            email: event.target.value
        })
    }
    handleSubmit = event => {
        console.log(`U: ${this.state.fullname} C: ${this.state.email} T: ${this.state.comments}`)
        alert(`U: ${this.state.fullname} C: ${this.state.email} T: ${this.state.comments}`);
    }
    
    render() {
        const { fullname, email, comments} = this.state;
        // const sendoToEmail = 'babaji108@hotmail.com';
        return (
        <div className={styles.container}>
            <h2 className={`${styles.textTitle} ${styles.colorText}`}>Contact Me</h2>
            <form className={styles.form} onSubmit={this.handleSubmit}>
                <label className={`${styles.colorText}`}>Full Name: </label>
                <input type="text" value={fullname} onChange={this.handleUserNameChange} />
                <label className={`${styles.colorText}`}>E-mail: </label>
                <input type="email" value={email} onChange={this.handleEmailChange} />
                <label className={`${styles.colorText}`}>Comments: </label>
                <textarea value={comments} onChange={this.handleCommentsChange} />
                <button type="submit" >Submit</button>
            </form>
        </div>
        )
    }
}

export default ContactForm